@import '/src/fontStyle.module.css';

.title {
  font-size: 2em;
  font-weight: 700;
  margin: 10px 0px;
  text-align: right;
  font-family: 'Maven Pro';
}

.brief {
  text-align: right;
  font-size: 1em;
  font-style: italic;
  padding-left: 20%;
  color: goldenrod;
}

.heading {
  font-size: 1.5em;
  font-weight: bold;
  margin: 10px 0px;
}
.image {
  width: auto;
  height: auto;
  overflow: hidden;
  margin-bottom: 20px;
}
.image img {
  max-height:100%; max-width:100%;
}
.image .caption {
  font-size: 0.8em;
  font-style: italic;
  color: gray;
  text-align: center;
}
.date {
  font-size: 0.8em;
  font-style: italic;
  text-align: right;
}
p {
  font-size: 1.1em;
}