html, body{
  height: 100%;
}

a {
  color: white;
}
  
.long {
  width: 100%;
  background-color: #010e19;
}
 
.container {
  height: 400px;
}
  
.scroll {
  opacity: 0.3;
  background-color: orange;
  width: 40px;
  height: 40px;
  position: fixed;
  bottom: 10px;
  right: 10px;
  border-radius: 5px;
  border: none;
  
}
.scroll:hover {
  opacity: 1;
}

.arrowUp {
  color: black;
  position: absolute;
  top: 50%;
  left: 50%;
  margin-top: -18px;
  margin-left: -18px;
  font-size: 1.2em;
  font-weight: bold;
}
@media only screen and (max-width: 600px) {
  .scroll {
    display: none;
  }
}